@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:ital,wght@0,400;0,500;1,400;1,500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:ital,wght@0,400;0,500;1,400;1,500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:ital,wght@0,400;0,500;1,400;1,500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:ital,wght@0,400;0,500;1,400;1,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.home_container{display:flex;align-items:center}.App{display:flex;flex-direction:row;background-color:#090909;color:#fff;width:100vw;height:100vh}

.header{padding:10px}.hamburger__menu{cursor:pointer}

.menu__container{background-color:#090909;height:100vh;width:20vw;padding:30px;display:flex;flex-direction:column;justify-content:flex-start;align-items:flex-start;-webkit-filter:drop-shadow(5px white);filter:drop-shadow(5px white)}.menu__container .button{margin-top:20px;font-size:xx-large;cursor:pointer;text-decoration:none;color:#fff}

.body__container{display:flex;flex-direction:row;justify-content:space-between}.body__container .text{object-fit:cover;display:flex;justify-content:center;font-family:"IBM Plex Mono",monospace;flex-direction:column;align-content:center}.body__container .text .hello__world{color:gray;font-size:large}.body__container .text .pitch{font-size:xx-large}.body__container .text .contact__button{color:#ccc;justify-content:flex-start;font-family:"IBM Plex Mono",monospace;white-space:normal;border:solid 1px white;margin-top:10px}.body__container .image{display:flex;width:30%;justify-content:center;align-items:center}.body__container .image .developer_image{width:100%;-webkit-filter:invert(96.6%);filter:invert(96.6%)}

